$font-headings: 'Miso', sans-serif;
$font-normal: 'Ubuntu', sans-serif;

$color-text: black;
$color-background: white;

// $color-footer-grey: #2B2B2B;

$color-cycle-pager-1: #94959A;
$color-cycle-pager-2: #f2f2f2;

$homepage-sections-opacity: 0.85;
$homepage-sections-background-carrers: rgba(78, 216, 195, $homepage-sections-opacity);


$sections: "news", "careers", "scoops", "games", "activities", "skills", "about";
$section-colors: ("news": #3e5fdb, "careers": #4ed8c3, "scoops": #ed3b5a, "games": #f9b653, "activities": #9d59e4, "skills": #52c36f, "about": #3e5fdb);
$section-background-colors: ("news": rgba(62, 95, 219, $homepage-sections-opacity), "careers": rgba(78, 216, 195, $homepage-sections-opacity), "scoops": rgba(237, 59, 90, $homepage-sections-opacity), "games": rgba(249, 182, 83, $homepage-sections-opacity), "activities": rgba(157, 89, 228, $homepage-sections-opacity), "skills": rgba(82, 195, 111, $homepage-sections-opacity), "about": rgba(62, 95, 219, $homepage-sections-opacity) );
$section-icons: ("homepage": "blue", "section-categories": "blue", "careers": "turquoise", "scoops": "red", "games": "orange", "activities": "purple");



$section-color-home: #3e5fdb;

$header-height-normal: 120px;
$header-height-small: 60px;
$time: 1s;

/* We should set the baseline/vertical spacing here */
$baseline: 24px;
$font-size-big: 36px;
$font-size-heading: 28px;
$font-size-heading2: 20px;
$font-size-heading3: 16px;
$font-size-normal: 14px;
$font-size-small: 12px;
$padding: 45px;

$content-max-width: 1250px;

@mixin bigtext() {
	font-family: $font-headings;
	letter-spacing: 0.1em;
	font-size: $font-size-big;
	line-height: $baseline*2;
}
@mixin heading() {
	font-family: $font-headings;
	letter-spacing: 0.1em;
	font-size: $font-size-heading;
	font-weight: 700;
	line-height: $baseline;
}
@mixin normaltext() {
	font-family: $font-normal;
	letter-spacing: 0.1em;
	font-size: $font-size-normal;
	line-height: $baseline;
}
@mixin smalltext() {
	font-family: $font-normal;
	font-size: $font-size-small;
	line-height: $baseline;
}
@mixin highlight() {
	font-family: $font-normal;
	font-weight: bold;
	line-height: 1.2em;
}

html, .pure-g [class *= "pure-u"] {
	@include normaltext();
}
button, input, select, textarea {
	@include normaltext();
	line-height: 1.2em;
}
html,body { height: 100%; }

body {
	background-color: $color-background;
	color: $color-text;
	overflow-x: hidden; // to keep sliding menus hidden
	@include normaltext();
}

.box-middle-ext { display: table; width: 100%; height: 100%; }
.box-middle-int { display: table-cell; vertical-align: middle; }


.fake-padding { height: $header-height-normal; }

ul {
	list-style: disc;
}
ol {
	list-style: decimal;
}
ul, ol {
	margin-left: 1em;
}
ul li, ol li {
	line-height: 2em;
	margin-left: 1em;
}

h1,h2,h3,h4,h5,h6,h7 {
	@include heading();
}
h2,h3 {
	font-size: $font-size-heading2;
}
h4,h5,h6 {
	font-size: $font-size-heading3;
}
p, ul, ol, table {
	margin-bottom: $baseline;
}
p:last-child {
	margin-bottom: 0px;
}
b, strong {
	font-weight: 700;
}
i, em {
	font-style: italic;
}

#header {
    background: url(/static/img/news_head2.png) no-repeat right 45px, url(/static/img/news_dote.png) no-repeat center top, #fff;
    background-position: 100% middle;
    background-size: contain, 100%, auto;
	position: fixed;
	width: 100%;
	background-color: white;
	text-align: center;
	top: 0;
	z-index: 999;
	.menu-bar {
		display: none;
		position: relative;
		z-index: 1;
	}
	.menu-bar a {
		border-bottom: 3px solid transparent;
	}
	.logo {
		margin-top: 12px;
		margin-bottom: 12px;
		display: inline-block;
		position: relative;
		z-index: 2;
		a {
			display: inline-block;
			line-height: 0px;
		}
		.logo-el {
			display: inline-block;
			height: 2.5em;
			width: 100px;
			font-size: $baseline;
			background-image: url(../img/logo.svg);
			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;
		}
	}
	.slogan {
		@include heading();
		line-height: 0.9em;
		text-transform: uppercase;
		-webkit-transition: opacity $time ease;
		transition: opacity $time ease;
		&.subtitle {
			font-size: $baseline;
			font-weight: bold;
		}
	}
	.button {
		position: absolute;
		top: 0;
		background-color: white;
		img {
			padding: 20px;
			opacity: .3;
			width: 24px;
		}
		&.mainmenu {
			left: 0;
		} 
		&.language {
			right: 0;
		} 
	}

	/* resize header when scrolling down */
	height: 120px;
	overflow: hidden;
	&.small {
		overflow: hidden;
		box-shadow: 0 0 3px 2px rgba(0, 0, 0, 0.35);
		.logo {
			.logo-el {
				height: 1.8em;
			}
		}
		.slogan {
			opacity: 0;
		}
	}
}
body.single #header, #header.small, .header-small {
	height: $header-height-small;
}
.homepage #header {
    background: url(/static/img/mainpage_1.png) no-repeat right top, url(/static/img/mainpage_dote.png) no-repeat left top, #fff;
}
#header, .homepage #header {
	-webkit-transition: background $time ease, background-position $time ease;
	transition: background $time ease, background-position $time ease;
}

.homepage #header.small {
	background: url(/static/img/mainpage_1.png) no-repeat 120% center, none, #fff;
}


#cover {
	height: 100%;
	text-align: center;
	overflow: hidden; // hides extra features when js is disabled
	a {
		color: inherit;
		text-decoration: none;
	}

	.feature-container {
		height: 100%;
		.cycle-slideshow {
			height: 100%;
		}
		.feature {
			width: 100%;
			height: 100%;
			background-position: center;
			background-repeat: repeat, no-repeat;
			background-size: auto, cover;
			// position: relative;
			.title-container {
				display: table;
				height: 100%;
				width: 90%;
				max-width: 780px;
				margin: auto;
				.title-subcontainer {
					display: table-cell;
					vertical-align: middle;
					// padding-bottom: 4em;
					color: white;
					.suptitle {
						@include normaltext();
						text-transform: none;
						font-size: $font-size-small;
					}
					.title {
						@include heading();
						font-weight: bold;
						text-transform: uppercase;
					}
					.subtitle {
						@include normaltext();
						text-transform: none;
						padding-top: 1em;
					}
				}
		}
	}
	.cycle-pager {
		z-index: 199;
		left: 1%; right: 1%;	// instead of width: 100%;
		position: absolute;
		bottom: 40px;
		padding: 5px $padding;
		margin: auto;
		span { 
			font-family: arial; font-size: $font-size-heading;
			width: 18px;
			cursor: pointer; 
			display: inline-block; color: $color-cycle-pager-1; 
			&.cycle-pager-active { color: $color-cycle-pager-2;}
		}
	}
	.arrow-pagedown {
		z-index: 199;
		display: block;
		width: 100%;
		position: absolute;
		bottom: 0;
		img {
			opacity: 0.6;
			height: $baseline*1.5;
			width: $baseline*1.5;
		}
		img:hover {
			opacity: 1;
		}
	}

	}
}

#main {
	position: relative;
	top: -$header-height-small;
	width: 1px;
	height: 1px;
	display: block;
}

#sections {
	// background-color: #dfd;
	color: white;
	a {
		color: inherit;
		text-decoration: none;
	}
	.section {
		height: 320px;
		background-size: cover;
		background-position: 50%;
		text-align: center;
	
		a.fill-container {
			display: block;
			height: 100%;
		}

		.title {
			@include bigtext();
			letter-spacing: inherit;
			text-rendering: optimizeLegibility; // enable kerning
			line-height: 1em;
			text-transform: uppercase;
			padding: 1em 0.2em;
		}
		.description {
			padding: 1em 1.5em;
			font-size: $font-size-small;
			line-height: 2.0em;
		}
		.expand {
			padding: 32px;
			img {
				height: 32px; width: 32px;
			}
		}
		.description, .expand {
			display: none;
		}
		&:hover {
			.box-middle-ext { background-size: 20% 10%; }
			.box-middle-int { vertical-align: bottom; }
			.description, .expand {
			display: block;
			}
		}
	}
	.section-1 {
		background-image: url(../img/sections/01.jpg);
		&:hover {
			.box-middle-ext { background-color: map-get($section-background-colors, 'careers'); }
		}
	}
	.section-2 {
		background-image: url(../img/sections/02.jpg);
		&:hover {
			.box-middle-ext { background-color: map-get($section-background-colors, 'scoops'); }
		}
	}
	.section-3 {
		background-image: url(../img/sections/03.jpg);
		&:hover {
			.box-middle-ext { background-color: map-get($section-background-colors, 'games'); }
		}
	}
	.section-4 {
		background-image: url(../img/sections/04.jpg);
		&:hover {
			.box-middle-ext { background-color: map-get($section-background-colors, 'activities'); }
		}
	}
	.section-5 {
		background-image: url(../img/sections/05.jpg);
		&:hover {
			.box-middle-ext { background-color: map-get($section-background-colors, 'skills'); }
		}
	}
	.section-6 {
		background-image: url(../img/sections/06.jpg);
		&:hover {
			.box-middle-ext { background-color: map-get($section-background-colors, 'about'); }
		}
	}
	.arrow-pagedown {
		display: block;
		width: 100%;
		padding-top: 10px;
		text-align: center;
		img {
			// opacity: 0.6;
			height: 14px;
			padding: 20px;
		}
	}
}


@each $section, $color in $section-icons {

	.#{$section} {
		.icon-space.normal {
			background-image: url(/static/img/icons/categories/icon_space_#{$color}.svg);
		}
		.icon-space.hover {
			background-image: url(/static/img/icons/categories/icon_space_#{$color}_over.svg);
		}
		.icon-earth.normal {
			background-image: url(/static/img/icons/categories/icon_earth_#{$color}.svg);
		}
		.icon-earth.hover {
			background-image: url(/static/img/icons/categories/icon_earth_#{$color}_over.svg);
		}
		.icon-heritage.normal {
			background-image: url(/static/img/icons/categories/icon_heritage_#{$color}.svg);
		}
		.icon-heritage.hover {
			background-image: url(/static/img/icons/categories/icon_heritage_#{$color}_over.svg);
		}
		.icon-navigation.normal {
			background-image: url(/static/img/icons/categories/icon_navigation_#{$color}.svg);
		}
		.icon-navigation.hover {
			background-image: url(/static/img/icons/categories/icon_navigation_#{$color}_over.svg);
		}
	}

}

#categories {
	max-width: $content-max-width;
	margin: auto;
	text-align: center;
	padding-top: 2*$padding;
	padding-bottom: $padding;
	.category {
		.title {
			@include highlight();
			width: 145px;
			margin: auto;
			font-size: $font-size-small;
		}
	}
	a {
		color: $section-color-home;
		text-decoration: none;
	}
}


.thumbnailed-list {
	position: relative;
	margin: auto;
	&.more-container-list {
		background-color: white;
	}
	.list-header {
		padding-top: $padding;
		padding-bottom: $padding;
		text-align: center;
		.selected {
			text-transform: capitalize;
		}
		.title {
			@include heading();
			text-transform: uppercase;
			padding-top: 30px;
			padding-bottom: 30px;
		}
		.filters {
			display: table;
			margin: auto;
			.filter-topic-text, .filter-topic-image {
				display: table-cell;
				width: 24%;
				font-size: $font-size-small;
				padding: 0 2em;
			}
			img {
				height: 2*$padding;
			}
			a {
				text-decoration: none;
				line-height: 0px;
				color: inherit;
				display: inline-block;
				&.selected {
					font-weight: bold;
				}
			}
		}
	}
	.list-item {
		height: $baseline*9;
		position: relative;
		.link-container, .color, .color-black, .text { position: absolute; height: 100%; width: 100%; }
		.link-container a { display: block; position: relative; width: 100%; height: 100%; }
		background-position: center;
		background-size: cover;
		color: white;
		text-align: center;
		.date, .age {
			font-size: $font-size-small;
			color: inherit;
		}
		.date {
			padding-top: $baseline;
		}
		.title {
			@include heading();
			font-size: $baseline;
			text-transform: uppercase;
			padding: 0 $padding;
		}
		.text {
			.preview {
				line-height: $baseline;
			}
		}
		.bottom {
			position: absolute;
			bottom: 0;
			width: 100%;
			text-align: center;

		}
		.theme img {
			height: 32px;
			width: 32px;
		}
		.expand {
			padding: 16px;
			line-height: 0px;
			img {
				height: 32px;
				width: 32px;
			}
		}
		.color-black {
			background-color: rgba(0, 0, 0, .3);
		}
		.date, .bottom, .age, .color, .theme {
			visibility: hidden;
			opacity: 0;
			transition: all 0.5s ease;
		}
		a:hover, a:focus {
			.date, .bottom, .age, .color, .theme {
				visibility: visible;
				opacity: 1;
			}
		}
	}
	.list-footer {
		padding-top: 30px;
		text-align: center;
		.load-more-button {
			display: inline-block;
			border: solid 2px gray;
			border-radius: 100px;
			padding: 4px 20px;
			text-decoration: none;
			color: inherit;
			&:hover, &:focus {
				color: white;
				background-color: map-get($section-colors, 'scoops');
				border-color: map-get($section-colors, 'scoops');
			}
		}
	}
}

#categories .category .icon, .thumbnailed-list .list-header .filters .icon {
	position: relative;
	height: 4*$baseline;
	width: 4*$baseline;
	margin: auto;
	.normal, .hover {
		position: absolute;
		top: 0;
		width: 100%;
		height: 100%;
		background-repeat: no-repeat;
		background-position: center; 
		background-size: contain;
	}
	.hover {
		visibility: hidden;
		opacity: 0;
		transition: all 0.5s ease;
	}
	&:hover {
		.hover {
			visibility: visible;
			opacity: 1;
		}
	}

}



/* Padded layout */
.row {
	width: 100%;
}
.padded, .padded-h {
	max-width: $content-max-width;
	width: 100%;
	position: relative;
	left: 0px;
	right: 0px;
	margin: auto;
}
.padded-top {
	padding-top: $padding;
}
.padded-bottom {
	padding-bottom: $padding;
}
.padded {
	padding: $padding;
}
.padded-h {
	padding-left: $padding;
	padding-right: $padding;
}
.padded-v {
	padding-top: $padding;
	padding-bottom: $padding;
}

.thumbnailed-list.news .padded {
	margin-top: $padding;
}


/* Column-based lists */
.padded-list, .list-3col, .list-4col {
	position: relative;
	letter-spacing: 0.1em;
	margin-left: -$padding;
	margin-bottom: 0px;
	right: -$padding;
	min-width: 100%;
	display: block;
    list-style: outside none none;
	li {
		display: inline-block;
		float: left;
		margin-bottom: $padding;
		margin-left: 0;
		border-right: $padding solid rgba(255, 255, 255, 0);
	}
	.list-item-container {
		margin: 0px;
	}
	.link-container a {
		color: inherit;
		text-decoration: none;
	}
	&:after {
		content: ".";
		display: block;
		height: 0;
		clear: both;
		visibility: hidden;
	}
}
.padded-list.under-filled, .list-3col.under-filled, .list-4col.under-filled {
	min-width: auto;
	text-align: center;
	li {
		float: none;
	}
}
.list-3col li, .padded-list li {
	width: 33.33333%;
}
.list-4col li {
	width: 25%;
}





.more-container-header {
	line-height: 0px;
}

table td, table th {
	@include normaltext();
	padding: $baseline/4 $baseline/2;
	border: 1px solid #444444;
}
table th {
	background-color: #444444;
	color: white;
}
table.mooc {
	margin-top: $padding;
	tr {
		background-color: $color-cycle-pager-2;
	}
	tr.mooc-extra td {
		display: none;
	}
	th {
		color: $color-background;
		background-color: map-get($section-colors, 'skills');
	}
	/* We define the last column first because we want the first column to win when there is only one column */
	th:last-child, td:last-child {
		padding-right: $padding;
		text-align: right;
	}
	th:first-child, td:first-child {
		padding-left: $padding;
		text-align: left;
	}
}


.content-card {
	max-width: $content-max-width;
	margin: auto;
	background-color: #fafafa;
	box-shadow: 0 0 6px 0px rgba(0, 0, 0, 0.2);

	.title-container {
		@include heading();
		padding: $padding/2 $padding;
		.title {
			text-transform: uppercase;
			font-size: $font-size-big;
			line-height: 1.5*$baseline;
			margin: 0;
		}
		.date, .teaser {
			font-size: $font-size-heading3;
			text-transform: uppercase;		
		}
	}
	.image-container {
		height: 400px;
		width: 100%;
		background-position: center;
		background-size: cover;
	}

	.content-container {
		padding: $padding;
		.alert {
			font-weight: bold;
			padding-top: 10px;
			padding-bottom: 10px;
		}
		.section-content {
			margin-bottom: $baseline;
		}
		.section-content:last-child {
			margin-bottom: 0;
		}
		.section-content p img {
			max-width: 100%;
			display: block;
		}

		.coolfact_title {
			@include heading();
			color: map-get($section-colors, 'scoops');
			text-transform: uppercase;
			font-size: $font-size-heading2;
			font-weight: bold;
			img {
				height: 14px; width: 14px;
			}
		}

		.content-main-column {
			padding: 0 $padding 0 0;
			.section-caption {
				text-transform: uppercase;
			}
			h2:first-child {
				padding-top: 0;
			}
		}

		.content-right-column {
			padding: 0;
			text-align: center;
			min-width: 100px;
			width: 25%;
			> div {
				border-top: solid thin grey;
				padding: $baseline 0;
				.caption {
					@include heading();
					font-size: $font-size-heading2;
					text-transform: uppercase;
					margin-bottom: $baseline/2;
				}
			}
			a {
				text-decoration: underline;
				color: inherit;
			}
			img.button {
				width: 1.5*$baseline;
				height: 1.5*$baseline;
				cursor: pointer;
			}
		}
	}
}

.content-page {
	padding-top: 100px;
	max-width: $content-max-width;
	margin: auto auto;
	padding: 0 $padding;
	.title {
		@include heading();
		text-transform: uppercase;
	}
	.subtitle {
		padding-bottom: 50px;
		color: map-get($section-colors, 'about');
	}
	.pure-u-1-4 > div {
		padding: 0 40px;
	}
	.title-container {
		text-align: center;
		padding: $padding;
		h1 { margin-top: 0; }
		p {
			padding-bottom: $padding;
			/*color: map-get($section-colors, 'about');*/
		}
		p:last-child {
			padding-bottom: 0;
		}
	}
	.page-about-goals {
		text-align: center;
	}
	.page-about-what {

	}
	.page-about-contact {
		text-align: center;
	}
	.page-about-team {
		text-align: center;
		img {
			padding: 10px 20px;
			vertical-align: middle;
		}
	}
	.smartembed {
		padding-top: $padding;
		padding-bottom: $padding;
	}
}


#subscribe {
	min-height: 280px;
	margin: $baseline 0;
	background: url(../img/subscribe/A-stars.png);
	background-repeat: no-repeat;
	background-position: center;
	background-size: auto 100%;
	color: white;
	text-align: center;
	.subscribe-container-1 {
		margin: 40px 0;
		background: url(../img/subscribe/background.jpg);
		background-position: center;
		background-size: cover;
		overflow: hidden;
	}
