@each $section in $sections {

	.section-#{$section} .small .select_language .sp-links,
	body.section-#{$section} #header.small {
	    background: url(/static/img/news_head2.png) no-repeat 150% center, none, map-get($section-colors, '#{$section}');
	}

	body.section-#{$section} {
		#cover {
			.feature-container {
				background-color: map-get($section-colors, $section);
			}
		}
		
		#header {
			.menu-bar a.menu-#{$section} {
				border-color: map-get($section-colors, '#{$section}');
			}
			.slogan {
				color: map-get($section-colors, $section);
			}
			.button {
				-webkit-transition: background $time ease;
				transition: background $time ease;
			}
			&.small {
				color: white;
				background-color: map-get($section-colors, '#{$section}');
				.button {
					background-color: map-get($section-colors, $section);
				}
				.sp-menu {
					background-color: map-get($section-colors, $section);
					.sp-button .button {
						&.mainmenu:after {
							background-image: url(../img/buttons/menu_white.svg);
						}
						&.language:after {
							background-image: url(../img/buttons/language_white.svg);
						}
					}
				}
				.logo .logo-el {
					background-image: url(../img/logo-white.svg);
				}
				.menu-bar a.menu-#{$section} {
					border-color: white;
				}
			}
		}
		.content-card {
			.title-container {
				color: map-get($section-colors, $section);
			}
		}
		.more-container-header .list-header {
			background-image: url(../img/more-title-bg-#{$section}.svg);
		}
		#footer .social-background {
			background-image: url(../img/footer-bg-social-#{$section}.svg);	
		}


		.wrapper_block_previews h2 {
			@include bigtext();
			text-align: center;
			font-weight: 700;
			text-transform: uppercase;
		}
		.wrapper_block_previews h3 {
			@include heading();
			text-align: center;
			color: map-get($section-colors, $section);
			margin-bottom: $baseline;
		}
	}
}



